// images references in the manifest
import "../../assets/icon-16.png";
import "../../assets/icon-32.png";
import "../../assets/icon-80.png";

/* global document, Office, Word */

Office.onReady(info => {
	if (info.host === Office.HostType.Word) {
		document.getElementById("sideload-msg").style.display = "none";
		document.getElementById("app-body").style.display = "flex";

		$(document).ready(function () {

			function showControls(_fileName) {
				$.ajax({
					url: "https://mybiz.decorfloor.ro/settings/parametersList?name=" + (_fileName ? _fileName : 'SSM.docx'), //+ document.getElementsByName('fileName')[0].value,
					type: "GET",
					dataType: "jsonp",
					error: function (err) {
						console.log(err);
						$("#content").html(JSON.stringify(err));
					},
					success: function (msg) {

						var str = ''

						msg.results.forEach(function (e) {
							str += '<div role="button" id="' + e.id + '" class="ms-Button ms-Button--hero ms-font-xl"><span class="ms-Button-label">' + e.name + '</span></div>'
						})
						$("#content").html(str);

						$('[role="button"]').each(function (index) {
							$(this).on('click', function () {
								run($(this).text())
							})
						})

					}
				});
			}

			function getFileUrl() {
				//Get the URL of the current file.
				Office.context.document.getFilePropertiesAsync(function (asyncResult) {
					var fileUrl = asyncResult.value;
					if (fileUrl == "") {
						console.log("The file hasn't been saved yet. Save the file and try again");
						showControls();
					} else {
						var fileName = asyncResult.value.url.split("/")[asyncResult.value.url.split("/").length-1]
						showControls(fileName);
						
					}
				});
			}

			getFileUrl();


		})


	}
});

export async function run(e) {
	return Word.run(async context => {
		/**
		 * Insert your Word code here
		 */

		// insert a paragraph at the end of the document.
		// const paragraph = context.document.body.insertParagraph("{{}}", Word.InsertLocation.end);

		var selectionRange = context.document.getSelection();
		selectionRange.insertText("{" + e.trim() + "}", "Before");

		// change the paragraph color to blue.
		// paragraph.font.color = "blue";

		await context.sync();
	});
}

